import { apiGet, apiPost } from "./api";

// 验证码 HelloWorld
export function getstrCode() {
  return new Promise((resolve) => {
    apiGet("/index.php/index/index/getcode").then((res) => {
      resolve(res);
    });
  });
}
// 登录 HelloWorld
export function getlogin(obj) {
  return new Promise((resolve) => {
    apiGet("/index.php/index/index/login", obj).then((res) => {
      resolve(res);
    });
  });
}
