import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    //登录
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    //整体框架
    path: "/first",
    name: "first",
    redirect: "/first/shou",
    component: () => import("../views/FirstView.vue"),
    children: [
      {
        //首页路由
        path: "/first/shou",
        component: () => import("../views/First/ShouData.vue"),
        redirect: "/first/shou/enter",
        children: [
          {
            //首页中的企业等子路由
            path: "/first/shou/enter",
            redirect: "/first/shou/enter/hour",
            component: () => import("../views/First/EnterData.vue"),
            children: [
              {
                //企业中的图表
                path: "/first/shou/enter/hour",
                component: () => import("../views/First/hour/OneHour.vue"),
              },
              {
                path: "/first/shou/enter/half",
                component: () => import("../views/First/hour/HalfHour.vue"),
              },
            ],
          },
          {
            //
            path: "/first/shou/server",
            component: () => import("../views/First/ServerData.vue"),
          },
          {
            path: "/first/shou/province",
            component: () => import("../views/First/ProvinceData.vue"),
          },
        ],
      },
      //企业管理
      {
        path: "/first/business",
        name: "business",
        component: () => import("../views/Bbusiness/BusView.vue"),
        redirect: "/first/business/super",
        children: [
          {
            path: "/first/business/super",
            component: () => import("../views/Bbusiness/super/SuperEnter.vue"),
          },
          {
            path: "/first/business/son",
            component: () => import("../views/Bbusiness/super/SonEnter.vue"),
          },
        ],
      },
      // 权限管理
      {
        path: "/first/system",
        name: "business",
        component: () => import("../views/System/systemData.vue"),
      },
      //呼转计费配置
      {
        path: "/first/charging",
        name: "charging",
        component: () => import("../views/Charging/ChargingData.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
  var local = localStorage.getItem("name");
  if (to.path === "/") {
    next();
  } else {
    // 判断是否已经登录
    if (local !== null && local !== "") {
      next();
    } else {
      next("/");
    }
  }
});

export default router;
