<template>
  <div class="box">
    <div class="login">
      <img src="../assets/二维码.jpg" width=160px height=160px
        style='position: absolute; right: 20px; top: 20px; border-radius: 10px;'>
      <div class="center">
        <img class="mihua" src="../assets/米话.png" alt="">
        <div class="login_left">
          <div class="login_pic"> </div>
        </div>
        <div class="login_right">
          <img src="../assets/呼叫中心.png" class="enterprise_language" alt="">
          <div class="ac">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px"
              class="demo-ruleForm" v-show="yin">
              <el-form-item prop="name">
                <el-input v-model.number="ruleForm.name" style="border: none!important" prefix-icon="el-icon-user"
                  class="input_css" placeholder="请输入用户名">
                </el-input>
              </el-form-item>
              <el-form-item prop="pass">
                <el-input type="password" v-model="ruleForm.pass" autocomplete="off" show-password
                  prefix-icon="el-icon-unlock" class="input_css" placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item prop="validate" style="position:relative">
                <el-input type="text" v-model="ruleForm.validate" autocomplete="off" prefix-icon="el-icon-set-up"
                  class="input_css" placeholder="请输入验证码">
                </el-input>
                <div class="ma" :style="bkcolor">{{ sum }}</div>
                <span type="primary" round @click="send()" :disabled="ClAss" style="margin-top: 20px;" class="yan">
                  {{ TextFont }}</span>
              </el-form-item>
            </el-form>


            <el-form label-width="100px" :model="zuceForm" status-icon :rules="rules" ref="zuceForm"
              class="demo-ruleForm" v-show="!yin">
              <el-form-item prop="name">
                <el-input v-model.number="zuceForm.name" prefix-icon="el-icon-user" placeholder="请输入用户名"
                  class="input_css">
                </el-input>
              </el-form-item>
              <el-form-item prop="emails" type="email">
                <el-input placeholder="请输入邮箱" v-model="zuceForm.emails" type="email" prefix-icon='el-icon-set-up'
                  class="input_css"></el-input>
              </el-form-item>

              <el-form-item prop="pass">
                <el-input type="password" v-model="zuceForm.pass" autocomplete="off" show-password
                  prefix-icon="el-icon-unlock" class="input_css" placeholder="请输入密码"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <el-button type="primary" round class="login_btn" @click="submitForm('ruleForm')">{{ loginText }}</el-button>
          <div class="login_jump">
            <div class="register" @click="register()">
              {{ qie }}
            </div>
            <div class="forgrt" @mouseenter="xian = !xian">
              忘记密码？
            </div>
            <div class="seek" v-show="xian" @mouseleave="xian = !xian">
              点击找回密码
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { getstrCode, getlogin, getregister } from '../api/https'
export default {
  data() {
    var ValiDate = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('验证码不能为空'));
      } else if (!(value == this.sum)) {
        console.log(this.sum, 11111, value);
        return callback(new Error('验证码不正确'));
      }
      callback();
    }
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('昵称不能为空'));
      }
      callback();
    };
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var confirmDate = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('邮箱不能为空'));
      }
      callback();
    };
    return {
      bkcolor: "",
      qie: '',
      loginText: "",
      yin: true,  //注册的切换
      xian: false,  //找回密码的切换
      TextFont: "换一张",
      sum: null,  //验证码
      ClAss: "",
      zuceForm: {
        pass: "",
        name: "",
        emails: ""
      },
      ruleForm: {
        pass: '',
        validate: '',
        name: '',
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        name: [
          { validator: checkAge, trigger: 'blur' }
        ],
        validate: [
          { validator: ValiDate, trigger: 'blur' }
        ],
        emails: [
          { validator: confirmDate, trigger: 'blur' }
        ]
      }
    };
  },
  mounted() {
    if (this.yin) {
      this.qie = "注册"
      this.loginText = "登录"
    } else {
      this.qie = "登录"
      this.loginText = "注册"
    }
  },
  methods: {
    randomHexColor() {
      let hex = Math.floor(Math.random() * 16777215).toString(16);
      this.bkcolor = 'background-color:' + '#' + (hex.length < 6 ? '0' + hex : hex);
    },
    register() {
      this.yin = !this.yin
      this.loginText = "注册"
      if (this.yin) {
        this.qie = "注册"
        this.loginText = "登录"
      } else {
        this.qie = "登录"
        this.loginText = "注册"
      }
    },
    send() {
      this.randomHexColor()
      getstrCode().then(res => {
        this.sum = res.msg
      })
    },
    submitForm(formName) {
      if (this.loginText == "登录") {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let obj = {
              vercode: this.ruleForm.validate,
              name: this.ruleForm.name,
              pwd: this.ruleForm.pass,
            }
            localStorage.setItem("name", this.ruleForm.name)
            getlogin(obj).then(res => {
              console.log(res);
              alert("欢迎光临")
              this.$router.replace({ path: `/first` })
            })
          }
        });
      } else if (this.loginText == "注册") {
        this.$refs["zuceForm"].validate((valid) => {
          if (valid) {
            let obj = {
              emails: this.zuceForm.emails,
              name: this.zuceForm.name,
              pwd: this.zuceForm.pass
            }
            console.log(obj);
            getregister(obj).then(res => {
              console.log(res);
              alert("注册成功")
              this.yin = !this.yin
              this.loginText = "登录"
            })
          }
        });
      }

    },
  },
  created() {
    this.send(),
      this.randomHexColor()
  }
}
</script>
